.slideshow-container {
    position: relative;
    width: 1248px;
    height: 400px; /* Adjust the height as needed */
    margin: auto;
  }
    
  .slideshow-container {
    overflow: hidden;
  }
  .slide {
    display: none;
    position: absolute;
    top: 0;
    left: 0;
    width:auto;
    height: 100%;
  }
  
  .slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    
  }
  
  .slide.active {
    display: block;
  }
  
.prev,.next ,.prev2 ,.next2{
    color: rgba(0, 0, 0, 0.642);
    height: 100px;
}
  
  .prev,.prev2 {
    left: 0px;
  }
  
  .next,.next2 {
        right: 0;
    border-radius: 3px 0 0 3px;
    margin-right: 6.1px;
  }
  


  
 
  
  .slide.active {
    display: block;
    animation: fade 1.8s ease-in-out;
  }
  
  @keyframes fade {
    0% {
      opacity: 0.2;
    }
    33%{
      opacity: 0.5;
    }
    65% {
      opacity: 0.7;
    }
    100% {
      opacity: 1;
    }
  }
  

  /* ------======FOr slide-show catalog======--------- */
  

  .prev2,
.next2 {
  cursor: pointer;
  position:absolute;
  bottom: -145%;
  width: auto;
  padding: 15px;
  color: white;
  font-weight: bold;
  font-size: 35px;
  transition: 0.6s ease;
  border-radius: 0 3px 3px 0;
  user-select: none;
}
.prev2 {
  left: 25px;
}
.next2 {
  right: 2px;
  border-radius: 3px 0 0 3px;
}

.prev2:hover,
.next2:hover {
  background-color: rgba(255, 255, 255, 0.507);
  color: rgba(0, 0, 0, 0.386);
  border: 0.4px solid white;
}