/* ====Slide show type Catalog===== */



.slide-catalog {
    /* position: relative; */
    margin:-150px 13px 0px 5px;
    display:flex;
    width: 1248px;
    height: 260px;
    border:2px solid rgb(218, 218, 218);
    border-radius:2px;
    /* box-shadow:4px 4px 4px 4px rgba(56, 158, 242, 0.116); */
    background-color: rgba(255, 255, 255);
    /* padding: 20px; */
    box-sizing: border-box;
    overflow: scroll visible;
}
#slide-catalog_img img{
    height:100%;
    /* margin:0 8px; */
    padding: 10px;
    box-sizing: border-box;
}
#slide-catalog_img {
    margin: 10px;
    background-color: rgba(243, 243, 243, 0.703);
    box-sizing: border-box;
height: 80%;
/* display: flex; */
/* position: absolute;
    top: 0;
    left: 0; */
}

